'use client';

import { useState, useEffect } from 'react';
import { useParams, useRouter } from 'next/navigation';
import Image from 'next/image';
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';
import Loading from '@/components/Loading';
import Error from '@/components/Error';
import { fetchListing } from '@/lib/api';
import { Listing } from '@/types';

export default function ListingDetailPage() {
  const params = useParams();
  const router = useRouter();
  const id = params.id as string;
  const [listing, setListing] = useState<Listing | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [selectedImageIndex, setSelectedImageIndex] = useState(0);

  useEffect(() => {
    loadListing();
  }, [id]);

  const loadListing = async () => {
    setLoading(true);
    setError(null);

    try {
      const data = await fetchListing(id);
      setListing(data);
    } catch (err: any) {
      setError(err.message || 'Failed to load listing');
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="flex min-h-screen flex-col">
        <Navbar />
        <Loading />
        <Footer />
      </div>
    );
  }

  if (error || !listing) {
    return (
      <div className="flex min-h-screen flex-col">
        <Navbar />
        <Error message={error || 'Listing not found'} onRetry={loadListing} />
        <Footer />
      </div>
    );
  }

  const roomTypeLabels = {
    single: 'Single Room',
    shared: 'Shared Room',
    entire: 'Entire Place',
  };

  const handleContactOwner = () => {
    router.push(`/contact/${id}`);
  };

  return (
    <div className="flex min-h-screen flex-col">
      <Navbar />

      <main className="flex-1 bg-white">
        <div className="mx-auto max-w-7xl px-4 py-8 sm:px-6 lg:px-8">
          <button
            onClick={() => router.back()}
            className="mb-6 text-blue-600 hover:text-blue-700"
          >
            ← Back to listings
          </button>

          <div className="grid grid-cols-1 gap-8 lg:grid-cols-3">
            <div className="lg:col-span-2">
              <div className="mb-6">
                <h1 className="mb-2 text-3xl font-bold text-gray-900">
                  {listing.title}
                </h1>
                <p className="text-lg text-gray-600">{listing.location}</p>
              </div>

              <div className="mb-8">
                <div className="relative h-96 w-full overflow-hidden rounded-2xl bg-gray-200">
                  {listing.images && listing.images.length > 0 ? (
                    <Image
                      src={listing.images[selectedImageIndex]}
                      alt={listing.title}
                      fill
                      className="object-cover"
                      sizes="(max-width: 1024px) 100vw, 66vw"
                    />
                  ) : (
                    <div className="flex h-full items-center justify-center">
                      <svg
                        className="h-16 w-16 text-gray-400"
                        fill="none"
                        viewBox="0 0 24 24"
                        stroke="currentColor"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth={2}
                          d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z"
                        />
                      </svg>
                    </div>
                  )}
                </div>

                {listing.images && listing.images.length > 1 && (
                  <div className="mt-4 grid grid-cols-4 gap-4">
                    {listing.images.slice(0, 4).map((image, index) => (
                      <button
                        key={index}
                        onClick={() => setSelectedImageIndex(index)}
                        className={`relative h-24 overflow-hidden rounded-lg ${
                          selectedImageIndex === index ? 'ring-2 ring-blue-600' : ''
                        }`}
                      >
                        <Image
                          src={image}
                          alt={`${listing.title} ${index + 1}`}
                          fill
                          className="object-cover"
                          sizes="(max-width: 1024px) 25vw, 16vw"
                        />
                      </button>
                    ))}
                  </div>
                )}
              </div>

              <div className="mb-8">
                <h2 className="mb-4 text-2xl font-bold text-gray-900">
                  About this place
                </h2>
                <p className="whitespace-pre-line text-gray-700">
                  {listing.description}
                </p>
              </div>

              {listing.amenities && listing.amenities.length > 0 && (
                <div className="mb-8">
                  <h2 className="mb-4 text-2xl font-bold text-gray-900">
                    Amenities
                  </h2>
                  <div className="grid grid-cols-2 gap-4 md:grid-cols-3">
                    {listing.amenities.map((amenity, index) => (
                      <div
                        key={index}
                        className="flex items-center gap-2 text-gray-700"
                      >
                        <svg
                          className="h-5 w-5 text-blue-600"
                          fill="none"
                          viewBox="0 0 24 24"
                          stroke="currentColor"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth={2}
                            d="M5 13l4 4L19 7"
                          />
                        </svg>
                        {amenity}
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>

            <div className="lg:col-span-1">
              <div className="sticky top-24 rounded-2xl border border-gray-200 bg-white p-6 shadow-lg">
                <div className="mb-6">
                  <div className="mb-4 flex items-baseline gap-2">
                    <span className="text-3xl font-bold text-gray-900">
                      ${listing.price}
                    </span>
                    <span className="text-gray-600">per month</span>
                  </div>
                  <span className="inline-block rounded-full bg-blue-100 px-3 py-1 text-sm font-medium text-blue-800">
                    {roomTypeLabels[listing.roomType]}
                  </span>
                </div>

                <button
                  onClick={handleContactOwner}
                  className="mb-4 w-full rounded-lg bg-blue-600 px-6 py-3 font-medium text-white transition-colors hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
                >
                  Contact Owner
                </button>

                <div className="border-t border-gray-200 pt-4">
                  <h3 className="mb-3 font-semibold text-gray-900">
                    Owner Information
                  </h3>
                  <div className="space-y-2 text-sm text-gray-600">
                    <p>
                      <span className="font-medium">Name:</span> {listing.owner.name}
                    </p>
                    <p>
                      <span className="font-medium">Email:</span> {listing.owner.email}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>

      <Footer />
    </div>
  );
}

